export default{dateTime(type,str){var date=new Date(str);var y=date.getFullYear();var m=date.getMonth()+1;m=m<10?('0'+m):m;var d=date.getDate();d=d<10?('0'+d):d;var h=date.getHours();h=h<10?('0'+h):h;var minute=date.getMinutes();var second=date.getSeconds();minute=minute<10?('0'+minute):minute;second=second<10?('0'+second):second;if(type==='-'){return y+'-'+m+'-'+d;}else if(type==='.'){return y+'.'+m+'.'+d;}else if(type==='/'){return y+'/'+m+'/'+d;}else if(type==='m/d h minute'){return m+'/'+d+' '+h+':'+minute;}else if(type==='my'){return{'m':m,'y':y}}else if(type==='y.m'){return y+'.'+m}else if(type==='y-m'){return y+'-'+m}else if(type==='y'){return y}else if(type==='m'){return m}else if(type==='y.m.d h minute'){return y+'.'+m+'.'+d+' '+h+':'+minute;}else{return y+'-'+m+'-'+d+' '+h+':'+minute+':'+second;};}}